import React from "react";
import ReactDOM from "react-dom";

export default class HASection extends React.Component {
    constructor(props) {
        super(props);
    }

    render() {
        /* jshint ignore:start */
        return (
            <section {...this.props}>
                {this.props.children}
            </section>
        );
        /* jshint ignore:end */
    }
}
